<template>
  <div class="home-page">
    <canvas id="chart"></canvas>
  </div>
</template>

<script>
  import Chart from '@/assets/js/Chart'
  import chartData from '@/assets/js/chartData'

export default {
  name: 'Home',
  data() {
    return {
      chart: null,
      chartData: []
    }
  },
  mounted() {
    this.chartData = chartData;
    this.chart = new Chart({
      el: '#chart',
      data: this.formatChartData(),
      bgImage: require(`../assets/image/bg.png`),
    });

    setInterval(() => {
      this.updateChartData();
    }, 300);
  },
  methods: {
    formatChartData() {
      return this.chartData.map(item => {
        if (item.type === 'image') {
          return item;
        }
        item.label = (+item.label).toFixed(item.mode === 'mpag' ? 3 : 1);
        return item;
      })
    },
    updateChartData() {
      this.chartData.forEach(item => {
        if (item.type === 'text') {
          if (Math.random() > 0.7) {
            this.updateItemText(item);
          }
        } else {
          if (Math.random() > (item.mode === 'big' ? 0.85 : 0.95)) {
            this.updateItemImage(item);
          }
        }
      });
      this.chart.setData(this.formatChartData());
    },
    updateItemText(item) {
      let random;
      switch (item.mode) {
        case 'percent':
          random = (Math.random() - 0.5) * 10;
          break;
        case 'mpag':
        case 'kpaa':
          random = (Math.random() - 0.5) / 5;
          break;
        case 'temp':
          random = (Math.random() - 0.5);
          break;
      }
      item.label = +item.label + random;
      item.label = item.label < 0 ? 0 : item.label;
      if (item.mode === 'percent') {
        item.label = item.label > 100 ? 100 : item.label;
      }
    },
    updateItemImage(item) {
      item.rotate = item.rotate || 0;
      item.rotate = item.rotate === 360 ? 0 : item.rotate;
      let count = 0;
      const timer = setInterval(() => {
        count++;
        item.rotate++;
        this.chart.setData(this.formatChartData());
        if (count === 90) {
          clearInterval(timer);
        }
      }, 10)
    }
  }
}
</script>

<style lang="scss">
  * {
    margin: 0;
    padding: 0;
  }
  #chart {
    width: 70%;
    height: calc(100vw * 0.7 * 871 / 1318);
  }
</style>
