import { get, round } from './utils'

class Chart {

  constructor (params = {}) {
    this.el = get(params, 'el');
    this.setData(get(params, 'data', []), false);
    this.initCanvas();
    this.initImages(params);
  }

  /**
   * 初始化canvas
   */
  initCanvas () {
    this.canvas = document.querySelector(this.el);
    this.context = this.canvas.getContext('2d');

    this.canvas.width = this.mapWidth =  this.canvas.offsetWidth;
    this.canvas.height = this.mapHeight = this.canvas.offsetHeight;
  }

  /**
   * 初始化图片，绘制画布
   * @param params
   */
  initImages (params) {
    const bgImage = get(params, 'bgImage', '');
    if (bgImage) {
      this.setBgImage(bgImage);
    }

    const imagesData = this.data.filter(item => item.type === 'image');
    if (imagesData && imagesData.length) {
      this.setDataImages(imagesData).then(() => {
        this.draw();
      })
    } else {
      this.draw();
    }
  }

  /**
   * 设置画布位点数据
   * @param data
   * @param redraw boolean 是否重绘
   */
  setData (data, redraw = true) {
    const fields = ['x', 'y', 'width', 'height'];
    this.data = data.map(item => {
      fields.forEach(key => {
        if (item[key]) {
          item[key] = +item[key];
        }
      });
      return item
    });
    if (redraw) {
      this.draw();
    }
  }

  /**
   * 设置背景图
   * @param bgImage
   */
  setBgImage (bgImage) {
    const image = new Image();
    image.src = bgImage;
    this.bgImage = image;
    image.onload = () => {
      this.draw();
    };
  }

  /**
   * 加载图片
   * @param data
   * @return {Promise<any>}
   */
  setDataImages (data) {
    let imgResolve;
    let loadCount = 0;
    data.forEach(item => {
      const image = new Image();
      image.src = item.url;
      image.onload = () => {
        loadCount++;
        if (loadCount === data.length) {
          imgResolve();
        }
      };
      item.image = image;
    });

    return new Promise(resolve => {
      imgResolve = resolve;
    });
  }

  /**
   * 重绘所有图像
   */
  draw () {
    this.clearMap();
    if (!this.data) {
      return;
    }
    this.data.forEach(item => {
      this.drawItem(item);
    })
  }

  /**
   * 重置画布
   */
  clearMap () {
    this.context.clearRect(0, 0, this.mapWidth, this.mapHeight);
    this.drawBgImage();
  }

  /**
   * 绘制背景图
   */
  drawBgImage () {
    if (!this.bgImage) {
      return;
    }
    this.context.drawImage(
      this.bgImage,
      0, 0,
      this.bgImage.width, this.bgImage.height,
      0, 0,
      this.mapWidth, this.mapHeight
    );
  }

  /**
   * 绘制子节点
   * @param data
   */
  drawItem (data) {
    let { type } = data;

    if (type === 'text') {
      this.drawItemText(data);
    } else {
      this.drawItemImage(data);
    }
  }

  /**
   * 绘制子节点文字
   * @param data
   */
  drawItemText (data) {
    const { label } = data;
    const { left, top } = this.getActualPosition(data);
    /*const text = {
      type: 'text',
      label: '1.00',
      x: 1,
      y: 0,
      textAlign: 'center',
      textBaseline: 'top',
      font: '',
      fillStyle: ''
    };*/

    this.context.textAlign = get(data, 'textAlign', 'center');
    this.context.textBaseline = get(data, 'textBaseline', 'center');
    this.context.font = get(data, 'font', '14px bold 黑体');
    this.context.fillStyle = get(data, 'fillStyle', '#333');
    this.context.fillText(label, left, top);
  }

  /**
   * 绘制子节点图像
   * @param data
   */
  drawItemImage (data) {
    /*const image = {
      type: 'image',
      url: '',
      image: '',
      x: 1,
      y: 0,
      width: 12,
      height: 20,
      rotate: 45
    };*/

    const image = get(data, 'image');
    const width = get(data, 'width', image.width);
    const height = get(data, 'height', image.height) || width;
    let { left, top } = this.getActualPosition(data);
    let rotate = get(data, 'rotate', 0);

    // 旋转
    const translateX = round(left + width / 2, 2);
    const translateY = round(top + height / 2, 2);
    rotate = Math.PI * (rotate / 180);
    if (rotate) {
      this.context.translate(translateX, translateY);
      this.context.rotate(rotate);
      left = -width / 2;
      top = -height / 2;
    }

    this.context.drawImage(
      image,
      0, 0,
      image.width, image.height,
      left, top,
      width,
      height
    );

    // 恢复旋转
    if (data.rotate) {
      this.context.rotate(-rotate);
      this.context.translate(-translateX, -translateY);
    }
  }

  /**
   * 获取实际坐标
   * @param data
   * @return {{top: number, left: number}}
   */
  getActualPosition (data) {
    const { x, y } = data;
    return {
      left: round(x * this.mapWidth, 2),
      top: round(y * this.mapHeight, 2),
    }
  }

  /**
   * 清空数据并重置画布
   */
  clear () {
    this.data = [];
    this.draw();
  }
}


export default Chart;
